.App {
  text-align: center;
}

:root {
  --cell_w: 300px;
  --cell_h: 300px;
  background: navy;
}

%transition {
  transition: transform 1.3s cubic-bezier(.75,-0.5,0,1.75); // easing function: elastic bouncing
}

// Symbolic3D Scene
.s3scene {
  position: absolute;
  left: calc(50% - var(--cell_w) / 2);
  top : calc(50% - var(--cell_h) / 2);
  perspective: 800px;
//  border: 1px dashed rgba(255,255,0,.5);  // debug
  // in JS
  width : var(--cell_w);
  height: var(--cell_h);
  margin-top: 40px;
  margin-left: 40px;
}

// Symbolic3D Body
.s3body {
  width : 100%;
  height: 100%;
  transform-style: preserve-3d;
//  border: 2px dotted rgba(255,0,255,.5);  // debug
//  background: rgba(255,0,255,.2);  // debug
  animation: aBody 40s linear infinite alternate;  // with animation, other transform will not work!
  @extend %transition;
}

// Symbolic3D Cell or Card
.s3cell {
  position: absolute;
  width : 100%;
  height: 100%;
  //font-size: calc(var(--cell_w) * .5);
  font-size: 50px;
  line-height: var(--cell_h);
  vertical-align: center;
  //border: 2px solid black;
  box-shadow: 1px 1px 4px 1px black; // offset-x, y, blur-radius, spread-radius
  //border-radius: 5px;
  background: rgba(250,250,250,.5);
  &:hover {
    box-shadow: 1px 1px 4px 1px black, 0 0 12px 8px #0ff;
  }
  @extend %transition;
}

@keyframes aBody {
  0%   { transform: rotateX(0deg)   rotateY(0deg); }
  50%  { transform: rotateX(-45deg) rotateY(-45deg); }
  100% { transform: rotateX(45deg)  rotateY(45deg); }
}

@mixin btnDemo($borderColor) {
  background-color: Gainsboro;
  border: 2px solid $borderColor;
  color: black;
  padding: 4px 4px;
  font: small "Courier New", sans-serif;
  font-weight: bold;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 10px;
  margin: 1px 2px;
  z-index: 100;
}

.btn-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  margin-top: 700px;
  margin-left: 10px;
  padding-top: 20px;
}

.test-btn {
  left: 10px;
  top:  10px;
  padding: 4px;
  margin:  2px;
  border-radius: 10%;
  @include btnDemo(#4caf50);
}

.test-btn:disabled,
test-btn[disabled] {
  border: 1px solid olive;
  background-color: darkgray;
  color: SlateGray;
}

